<template>
  <div>
    <Navbar :title="title" :lflag="lflag" @go="goMymess"></Navbar>

    <ul class="mar_L32 mar_T12">
      <!-- 到搜索好友界面 -->
      <li @click="addFriend">
        <img src="../../../../static/imgs/addfriend.png" alt />
        <p>添加好友</p>
      </li>
      <!-- 到新的好友界面，点进去加载考虑 -->
      <li>
        <img src="../../../../static/imgs/newfriend.png" alt />
        <p @click="jump">新的好友</p>
      </li>
    </ul>
     <!-- <h1>{{msg}}--{{this.userId}} --loc:{{localToken}}</h1> -->
       <!-- <h1>{{this.list}}</h1> -->
    <div class="friendlist" v-if="islist">
      <!-- 我的所有好友放x? 需要再mounted里面-->
     
      <p>好友列表</p>
      <!-- <button @click="onClickLeft">按钮测试</button>
      <h2 v-if="ish">哈哈哈</h2>-->
      <!-- 路由跳转到好友介绍 -->
      <div class="list mar_L32" v-for="(item,index) in list" :key="index">
        <router-link :to='"/FIntroduce/"+index +"/"+ item.user_id'>
          <img :src="item.avatar" alt />
          <p>{{item.username}}</p>
        </router-link>
      </div>
    </div>
    <div class="load">
      <van-loading type="spinner" color="#3FC4E5" v-if="isload" />
    </div>
    <div class="msg" v-if="ismsg">
      <p>你还没有好友，请去添加好友！</p>
      <button @click="friend">添加好友</button>
    </div>

    <van-popup v-model="iserr" round>网络连接错误，请检查网络！！</van-popup>
    <!-- <h1>ios:{{this.$store.state.self.token}} </h1>
    <h1>{{this.$store.state.self.userId}}</h1>
    <h1>{{this.list}}</h1>-->
  </div>
</template>

<script>
import qs from "qs";
import Navbar from "../public/navbar";
// import { mapState } from "vuex";
// import { mapMutations } from "vuex";
export default {
  props: {},
  data() {
    return {
      ish: false,
      iserr: false,
      ismsg: false,
      isload: true,
      islist: false,
      msg: "没传过来",
      localToken:'',
      question: "前输入文字",
      title: "我的好友",
      lflag: true,
      list: [
        { username: "", avatar: "" } ///static/imgs/touxiang.png
      ],
      userId: this.$store.state.self.userId,
      token: this.$store.state.self.token,
      navigator: ""
    };
  },
  watch: {
    // msg(){
    //   this.question ="正在输入"
    // }
  },
  beforeCreate() {
    //this.$router.go(0)
  },
  created() {
    this.navigator = navigator.userAgent.match(/android/i);
    //     this.axios.post('/entry',qs.stringify({
    //        user_id:this.userId,
    //        token: this.token
    //       })).then(res=>{
    //         console.log(res.data.data)
    //         this.list = res

    //      if(this.list.length>0){
    //           this.isload=false
    //           this.islist = true
    //           }else{
    //               this.isload=false
    //             this.ismsg= true
    //           }
    //       }).catch(err=>{
    //         console.log(err.response.status)
    //         if(err.response.status == 500){
    // this.iserr =true
    // this.isload=true
    //         }

    //       })

    // console.log(this.$store.state)
  },
  mounted() {
    // window.callJsFunction = this.callJsFunction;
    //ios
    //window.callIosFunction = this.callIosFunction;

    let token = localStorage.getItem("token");
    //console.log(token === null);
this.localToken = token
    if (token === null) {
      if (navigator.userAgent.match(/(iPhone|iPod|iPad);?/i) || navigator.userAgent.match(/android/i)) {
      window.callIosFunction = this.callIosFunction;
      window.callJsFunction = this.callJsFunction;
        //console.log(navigator.userAgent.match(/(iPhone|iPod|iPad);?/i))
      } else {
        this.axios
          .post(
            "/entry",
            qs.stringify({
              user_id: this.userId,
              token: this.token
            })
          )
          .then(res => {
           // console.log(res.data.data);
            this.list = res.data.data;

            if (this.list.length > 0) {
              this.isload = false;
              this.islist = true;
            } else {
              this.isload = false;
              this.ismsg = true;
            }
          })
          .catch(err => {
            console.log(err.response.status);
            if (err.response.status == 500) {
              this.isload = true;
              this.iserr = true;
            }
          });
      }
    } else if (token === this.$store.state.self.token) {
      this.axios
        .post(
          "/entry",
          qs.stringify({
            user_id: this.userId,
            token: this.token
          })
        )
        .then(res => {
         // console.log(res.data.data);
          this.list = res.data.data;

          if (this.list.length > 0) {
            this.isload = false;
            this.islist = true;
          } else {
            this.isload = false;
            this.ismsg = true;
          }
        })
        .catch(err => {
          //   console.log(err.response.status)
          if (err.response.status == 500) {
            this.iserr = true;
            this.isload = true;
          }
        });
    } else {
      window.callIosFunction = this.callIosFunction;
       window.callJsFunction = this.callJsFunction;
    }
  },
  methods: {
    onClickLeft() {
      window.android.onClickLeft();
    //  this.ish = true;
    },
    //ios交互
    callIosFunction(ios) {
      this.msg = ios;
      //JSON.parse(ios)
      //this.msg = JSON.parse(ios).token
      this.$store.commit("changeToken", JSON.parse(ios).token);
      localStorage.setItem("token", JSON.parse(ios).token);
      this.axios
        .post("/userId", {
          token: JSON.parse(ios).token
        })
        .then(res => {
          this.userId = res.data.data.user_id;
          this.$store.commit("changID", res.data.data.user_id);
        })
        .then(() => {
          this.axios
            .post(
              "/entry",
              qs.stringify({
                user_id: this.userId,
                token: this.msg
              })
            )
            .then(res => {
             // console.log(res.data.data);
              this.list = res.data.data;

              if (this.list.length > 0) {
                this.isload = false;
                this.islist = true;
              } else {
                this.isload = false;
                this.ismsg = true;
              }
            });
        })
        .catch(err => {
         // console.log(err.response.status);
          if (err.response.status == 500) {
            this.iserr = true;
            this.isload = true;
          }
        });
    },
  //安卓交互
     callJsFunction(android) {
      this.msg = JSON.parse(JSON.stringify(android)).token;
      this.$store.commit(
        "changeToken",
        JSON.parse(JSON.stringify(android)).token
      );
            localStorage.setItem('token',this.msg)
      this.axios
        .post("/userId", {
          token:  JSON.parse(JSON.stringify(android)).token
        })
        .then(res => {
          this.userId = res.data.data.user_id;
          this.$store.commit("changID", res.data.data.user_id);
        })
        .then(() => {
          this.axios
            .post(
              "/entry",
              qs.stringify({
                user_id: this.userId,
                token: this.msg
              })
            )
            .then(res => {
             // console.log(res.data.data);
              this.list = res.data.data;

              if (this.list.length > 0) {
                this.isload = false;
                this.islist = true;
              } else {
                this.isload = false;
                this.ismsg = true;
              }
            });
        })
        .catch(err => {
         // console.log(err.response.status);
          if (err.response.status == 500) {
            this.iserr = true;
            this.isload = true;
          }
        });
    },
    friend() {
      this.$router.push("Searchfriend");
    },
    //左键返回
    goMymess() {
      if (navigator.userAgent.match(/(iPhone|iPod|iPad);?/i)) {
        window.webkit.messageHandlers.onClickLeft.postMessage(null);
        //[0] =='Android'
      } else if (navigator.userAgent.match(/android/i)) {
        //window.androidDelegate.getCall()
        //this.$router.go(-1)
        window.android.onClickLeft();
      } else {
        this.$router.go(-1);
      }
    },
    test1() {
      this.axios
        .post(
          "/blackList",
          qs.stringify({
            user_id: this.userId,
            //del_user_id: this.$route.params.userId,
            token: this.msg
          })
        )
        .then(res => {
          console.log(res.data);
        })
        .catch(err => {
          console.log(err.response.status);
          if (err.response.status == 500) {
            this.iserr = true;
            this.isload = true;
          }
        });
    },
    test() {
      this.$router.push("SelectFriend");
    },
    addFriend() {
      this.$router.push("/Searchfriend");
    },
    jump() {
      this.$router.push("/NewFriend");
    }
  },

  components: {
    Navbar
  }
};
</script>

<style scoped lang="less">
.msg {
  margin: 1rem auto;
  p {
    font-size: 0.32rem;
    text-align: center;
  }
  button {
    font-size: 0.28rem;
    margin-left: 2.7rem;
    margin-top: 0.1rem;
  }
}
.load {
  margin: 3rem 3rem;
}
ul {
  margin-top: 1.28rem;
  li {
    display: flex;
    margin-bottom: 0.24rem;

    img {
      width: 0.96rem;
      height: 0.96rem;
    }
    p {
      color: #000000;
      font-size: 0.36rem;
      margin-left: 0.25rem;
      margin-top: 0.31rem;
    }
  }
}
.friendlist {
  margin-top: 0.28rem;
  p {
    margin-left: 0.36rem;
    padding-top: 0.28rem;
    padding-bottom: 0.28rem;
    color: #000000;
    font-size: 0.36rem;
    font-weight: 400;
  }
  .list {
    a {
      display: flex;
      margin-bottom: 0.24rem;
    }
    img {
      width: 0.96rem;
      height: 0.96rem;
      border-radius: 50%;
    }
  }
}
.van-popup {
  width: 5.4rem;
  height: 1.56rem;
  text-align: center;
  line-height: 1.56rem;
  color: #000000;
  font-size: 0.32rem;
}
</style>



